/* Draw Box Animation */
@keyframes drawBox {
  0% {
    transform: scaleX(0);
    opacity: 0;
  }
  50% {
    transform: scaleX(1);
    opacity: 0.5;
  }
  100% {
    transform: scaleX(1);
    opacity: 1;
  }
}

.draw-panel {
  transform-origin: left;
  animation-delay: 0.5s;
}

/* Base button style */
.button-style {
  padding: 15px 30px;
  border: none;
  border-radius: 8px;
  font-size: 16px;
  color: #ffffff;
  background: linear-gradient(90deg, #1F6FEB, #58A6FF); /* Original gradient */
  cursor: pointer;
  text-decoration: none;
  display: inline-block;
  transition: transform 0.3s ease, background 0.5s ease, box-shadow 0.5s ease;
}

/* Hover effect */
.button-style:hover {
  transform: scale(1.1); /* Slightly enlarge the button */
  background: linear-gradient(90deg, #1553b1, #174e8d);
  box-shadow: 0px 4px 10px rgb(97, 147, 255);
}


.hacker-line,
.scrolling-text {
  animation: pulseLine 2s infinite;
  background: linear-gradient(180deg, #58A6FF, transparent);
  position: absolute;
  width: 5px;
}
